@use "../../styles/partials/" as *;

.cardContainer{
    display: flex;
    padding: 8px;
    border: 1px solid map-get($map: $grey-color, $key: grey-300);
    border-radius: map-get($map: $scale, $key: 8);
    background-color: white;

    .ticketHeader {
        display: flex;
        flex-direction: row;
        flex-flow: row;
        justify-content: space-between;
    }

    .ticketSummary {
        display: flex;
        flex: 1;
        gap: map-get($map: $scale, $key: 8);
        flex-direction: column;
        padding: 8px;

        :local(.title) {
            @extend .sub-hero-text-regular-medium;
        }
        :local(.about){
            @extend .hero-text-regular;
            font-weight: 500;
        }
        :local(.icon img) {
            width: 16px;
            height: 16px;
            color: map-get($map: $grey-color, $key: grey-500);
        }
    }
}